'use client' 
import styled from 'styled-components'

const SectionTitle = ({ children, color="red", lineColor="gray", tag="h1" }) => {
    return (
        <Title color={color} $lineColor={lineColor} as={tag}>{ children }</Title>
    )
}

export default SectionTitle

const Title = styled.h1`
    margin: 0;
    position: relative;
    color: var(--${props => props.color});
    font-weight: 900;
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 1px;
    @media (min-width: 0px) and (max-width: 991px){
        font-size: 2.25rem;
        margin-bottom: 3rem;
    }
    @media (min-width: 992px){
        font-size: 3.5rem;
        margin-bottom: 4.5rem;
    }
    &:after{
        content: '';
        position: absolute;
        bottom: -1.25rem;
        left: 0;
        right: 0;
        margin: auto;
        height: 2px;
        width: 80%;
        max-width: 450px;
        background: var(--${props => props.$lineColor});
    }
`