'use client'

import styled from "styled-components"

const Column = ({ className="",  children, maxW="auto" }) => {
    return (
        <ColumnWrap $maxW={maxW} className={"col" + (className ? " " + className : "")}>
            { children }
        </ColumnWrap>
    )
}

export default Column

const ColumnWrap = styled.div`
    flex: 1;
    ${props => props.$maxW ? `
        max-width: ${props.$maxW} !important;
    ` : ``}
    @media (min-width: 0px) and (max-width: 991px){
        width: 100%;
        flex: 0 0 100%;
    }
`