import styled, { css } from "styled-components"

const Button = ({ type = "red", children, download=false, href="#", onClick = null, disabled = null, target="_self", rel="noopener noreferrer" }) => {

    const onClickEvent = (e) => {
        if(!onClick) return
        onClick(e)
    }

    return (
        <ButtonWrap 
            onClick={(e) => { onClickEvent(e) }} 
            disabled={disabled ? "1" : ""} 
            type={type} 
            download={download}
            target={target} 
            rel={rel} 
            href={href}>
                <span>{ children }</span>
            </ButtonWrap>
    )
}

export default Button

const ButtonWrap = styled.a`
    position: relative;
    display: block;
    padding: 0.6rem 1.35rem;
    color: white;
    transition: all .3s ease-out;
    text-transform: uppercase;
    letter-spacing: 3px;
    font-size: 1.125rem;
    font-weight: bold;
    max-width: max-content;
    border-radius: 3rem;
    overflow: hidden;
    span{
        position: relative;
        z-index: 5;
    }
    &:before{
        content: '';
        width: 100%;
        transition: all .2s ease-out;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
    }
    ${props => props.type==='blue' ? css`
        border: 3px solid var(--blue);
        background: var(--blue);
        border-radius: 5rem;
        padding: 0.5rem 2rem;
        &:before{
            background: var(--blue);
        }
        @media (min-width: 992px){
            &:hover{
                background: none;
                color: var(--blue);
                &:before{
                    width: 0%;
                }
            }
        }
    ` : css`
        border: 3px solid var(--red);
        background: var(--red);
        @media (min-width: 992px){
            &:before{
                background: var(--red);
            }
            &:hover{
                background: none;
                color: var(--red);
                &:before{
                    width: 0%;
                }
            }
        }
    `}
    ${props => props.type==='footer' ? css`
        border: 2px solid white;
        border-radius: 6rem;
        font-size: 1rem;
        letter-spacing: 4px;
        padding: 0.7rem 1.25rem;
        &:before{
            width: 0%;
            background: white;
        }
        &:hover{
            background: white;
            color: var(--red);
            &:before{
                width: 100%;
            }
        }
    ` : ``}
    ${props => props.disabled==="1" ? css`
        opacity: 0.3;
        pointer-events: none;
    ` : ``}
`