'use client'

import styled from "styled-components"

const Row = ({ className="", children, alignItems="center", justifyContent="space-between" }) => {
    return (
        <RowWrap $alignItems={alignItems} $justifyContent={justifyContent} className={"row" + (className ? " " + className : "")}>
            { children }
        </RowWrap>
    )
}

export default Row

const RowWrap = styled.div`
    display: flex;
    align-items: ${props => props.$alignItems};
    justify-content: ${props => props.$justifyContent};
    flex-wrap: wrap;
`