import { useEffect } from "react"

import './App.scss'

import Header from "./components/Header"

import Hero from "./components/sections/Hero"
import About from "./components/sections/About"
import Benefits from "./components/sections/Benefits"
import Details from "./components/sections/Details"
import VideoLibrary from "./components/sections/VideoLibrary"
import UniversityCourses from "./components/sections/UniversityCourses"
import Customers from "./components/sections/Customers"
import Footer from "./components/Footer"
import Form from "./components/sections/Form"

export default function Home() {

	useEffect(() => {
		const script = document.createElement('script');
		script.src = "https://assets.adobedtm.com/21fa3c337531/9dd4b1d7bd35/launch-025f15007675.min.js";
		script.async = true;
		document.head.appendChild(script);
	
		return () => {
			document.head.removeChild(script);
		};
	}, []);

	return (
		<>
			<Header />
				<main>
					<Hero />
					<About />
					<Benefits />
					<Details />
					<VideoLibrary />
					<UniversityCourses />
					<Customers />
					<Form />
				</main>
			<Footer />
		</>
	)
}