'use client'
import styled from 'styled-components'

import { Swiper, SwiperSlide } from 'swiper/react'
import { Navigation, Pagination } from 'swiper/modules'

import 'swiper/css'
import 'swiper/css/pagination'
import 'swiper/css/navigation'

import SectionTitle from '../elements/SectionTitle'

const videos = [
    {
        url: './videos/Video 1.mp4',
        poster: './videos/Video 1 Poster.jpg'
    },
    {
        url: './videos/Video 2.mp4',
        poster: './videos/Video 2 Poster.jpg'
    },
    {
        url: './videos/Video 3.mp4',
        poster: './videos/Video 3 Poster.jpg'
    },
]

const VideoLibrary = () => {

    const pagination = {
        el: '.swiper-pagination',
        clickable: true,
        renderBullet: function (index, className) {
            return `<div class="${className}"><img width="100" height="58" src="${videos[index].poster}" /></div>`
        },
    }

    return (
        <VideoLibrarySection>
            <TitleWrap>
                <SectionTitle tag="h3">Explore our Video Library</SectionTitle>
            </TitleWrap>
            <VideosWrap>
                <VideoSliderWrap>
                    <Swiper
                        slidesPerView={1}
                        pagination={pagination}
                        navigation={{
                            nextEl: '.swiper-button-next',
                            prevEl: '.swiper-button-prev',
                        }}
                        modules={[Pagination, Navigation]}>
                            { videos && videos.map((video, index) => {
                                return (
                                    <SwiperSlide key={index}>
                                        <video poster={video.poster} controls={true} autoPlay={false} playsInline={true}>
                                            <source src={video.url} />
                                        </video>
                                    </SwiperSlide>
                                )
                            }) }
                    </Swiper>
                    <div className="swiper-pagination"></div>
                    <div className="swiper-button-prev"></div>
                    <div className="swiper-button-next"></div>
                </VideoSliderWrap>
            </VideosWrap>
        </VideoLibrarySection>
    )
}

export default VideoLibrary

const VideoLibrarySection = styled.section`
    @media (min-width: 0px) and (max-width: 991px){ 
        padding: 4rem 2rem;
        padding-bottom: 8.5rem;
    }
    @media (min-width: 992px){ 
        padding: 8rem 0;
        padding-bottom: 6rem;
    }
`

const TitleWrap = styled.div`
    text-align: center;
`

const VideosWrap = styled.div`
    position: relative;
    &:before{
        content: '';
        display: block;
        position: absolute;
        width: 100%;
        height: 60%;
        background: var(--red);
        top: 0;
        bottom: 0;
        margin: auto;
        left: 0;
    }
`

const VideoSliderWrap = styled.div`
    position: relative;
    width: 100%;
    max-width: 600px;
    margin: 0 auto;
    video{
        width: 100%;
        height: auto;
        max-height: 335px;
        object-fit: cover;
    }

    .swiper-button-prev,
    .swiper-button-next{
        transition: all .3s ease-out;
        @media (min-width: 0px) and (max-width: 991px){
            display: none;
        }
        &:after{
            width: 0;
            height: 0;
            border-top:  1.25rem solid transparent;
            border-bottom:  1.25rem solid transparent;
            text-indent: -9999px;
            transition: all .3s ease-out;
        }
        &:hover{
            &:after{
                opacity: 0.5;
            }
        }
    }
    .swiper-button-prev{
        left: -3rem;
        &:after{
            border-right: 1.25rem solid white;
        }
    }
    .swiper-button-next{
        right: -3rem;
        &:after{
            border-left: 1.25rem solid white;
        }
    }
    .swiper{
        position: relative;
    }
    .swiper-pagination{
        position: absolute;
        display: flex;
        gap: 1.5rem;
        bottom: -0.5rem;
        transform: translateY(100%);
        justify-content: center;
        .swiper-pagination-bullet{
            background: none;
            width: 20%;
            height: auto;
            margin: 0;
            transition: all .3s ease-out;
            &:hover{
                opacity: 0.8;
            }
            img{
                width: 100%;
                height: auto;
                max-height: 65px;
                object-fit: cover;
                background: black;
            }
        }
    }
`