import { useEffect, useState } from "react"

import styled, { css } from 'styled-components'

import Container from './layouts/Container'
import Row from './layouts/Row'
import Button from "./elements/Button"

const Header = () => {

    const [active, setActive] = useState('0')
    const [isSticky, setIsSticky] = useState(false)

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 100) {
                setIsSticky(true);
            } else {
                setIsSticky(false);
            }
        };

        window.addEventListener('scroll', handleScroll);

        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <HeaderWrap $issticky={isSticky ? '1' : '0'}>
            <Container>
                <Row>
                    <LogoLink href="#">
                        <img alt="Site Logo" width="180" height="56" src="./images/logo.svg" />
                    </LogoLink>
                    <Nav>
                        <BurgerButton $active={active ? "1" : ""} onClick={() => { setActive(!active) }}><span /></BurgerButton>
                        <Menu $active={active ? "1" : "0"}>
                            <MenuLink><a href="#about">About</a></MenuLink>
                            <MenuLink><a href="#benefits">Benefits</a></MenuLink>
                            <MenuLink><a href="#courses">Courses</a></MenuLink>
                            <ButtonLink>
                                <Button type="blue" href="#signup">Sign Up</Button>
                            </ButtonLink>
                            <SocialIcon>
                                <a rel="noopener noreferrer" target="_blank" href="https://www.linkedin.com/showcase/dupont-cyrel/">
                                    <img alt="Linkedin" width="24" height="24" src="./images/icons/linkedin.svg" />
                                </a>
                            </SocialIcon>
                        </Menu>
                    </Nav>
                </Row>
            </Container>
        </HeaderWrap>
    )
}

export default Header

const Nav = styled.nav`
    display: flex;
    align-items: center;
    justify-content: space-between;
`

const BurgerButton = styled.div`
    @media (min-width: 0px) and (max-width: 991px){
        display: flex;
        width: 2.825rem;
        height: 2rem;
        position: relative;
        flex-direction: column;
        justify-content: space-between;
        padding: 0.5rem;
        &::after,
        &::before,
        span{
            content: '';
            width: 100%;
            height: 1px;
            background-color: var(--red);
            transition: all .3s ease-out;
        }
        
        ${props => props.$active!=="1" ? css`
            span{
                opacity: 0;
                transform: translateX(100%);
            }
            &::before{
                transform: translateY(7px) rotateZ(45deg);
            }
            &::after{
                transform: translateY(-7px) rotateZ(-45deg);
            }
        ` : ``}

    }
    @media (min-width: 992px){
        display: none;
    }
`

const SocialIcon = styled.li``

const Menu = styled.ul`
    align-items: center;
    justify-content: space-between;
    list-style: none;
    margin: 0;
    padding: 0;
    @media (min-width: 0px) and (max-width: 991px){
        display: flex;
        flex-direction: column;
        position: absolute;
        right: 0;
        background: #ffffffe2;
        width: 100%;
        max-width: 350px;
        padding: 1rem 1.5rem;
        transform: translateX(${props => props.$active==='1' ? `100%` : `0%`});
        transition: all .3s ease-out;
        box-shadow: 0px 10px 15px #00000014;
        border-bottom-left-radius: 1rem;
        top: 3.35rem;
    }
    @media (min-width: 992px){
        display: flex;
    }
    li{
        @media (min-width: 0px) and (max-width: 991px){
            width: 100%;
            flex: 0 0 100%;
            text-align: center;
        }
        a{
            display: flex;
            font-weight: bold;
            font-size: 0.875rem;
            text-transform: uppercase;
            letter-spacing: 3px;
            @media (min-width: 0px) and (max-width: 991px){
                display: block;
                margin: 0;
                padding: 0.825rem;
            }
            @media (min-width: 992px){
                margin: 0 1.5rem;
                img{
                    float: left;
                }
            }
        }
    }
    ${SocialIcon}{
        a{
            @media (min-width: 0px) and (max-width: 991px){
                margin: 0;
            }
            @media (min-width: 992px){
                margin-right: 0;
            }
        }
    }
`

const LogoLink = styled.a`
    img{
        float: left;
        transition: all .1s ease-out;
        @media (min-width: 0px) and (max-width: 991px){
            width: 120px;
            height: auto;
        }
        @media (min-width: 992px){
            width: 200px;
            height: auto;
        }
    }
`

const MenuLink = styled.li`
    a{
        position: relative;
        color: var(--red);
        padding: 0.25rem 0;
        &:after{
            content: '';
            width: 100%;
            height: 3px;
            position: absolute;
            background: var(--red);
            left: 0;
            opacity: 0;
            bottom: 0px;
            transition: all .3s ease-out;
        }
        @media (min-width: 992px){
            &:hover{
                &:after{
                    opacity: 1;
                    bottom: -3px;
                }
            }
        }
    }
`

const ButtonLink = styled.li`
    @media (min-width: 0px) and (max-width: 991px){
        display: flex;
        justify-content: center;
        a{
            padding: 0.5rem 1rem !important;
        }
    }
`


const HeaderWrap = styled.header`
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    transition: all .3s ease-out;
    z-index: 9999;
    @media (min-width: 0px) and (max-width: 991px){
        background: #ffffffe2;
        padding: 0.5rem 0;
    }
    ${props => props.$issticky==='1' ? css`
        background: #ffffffe2;
        backdrop-filter: blur(5px);
        padding: 0.5rem 0;
        @media (min-width: 0px) and (max-width: 991px){
            ${Menu}{
                top: 3.35rem;
            }
        }
        @media (min-width: 992px){
            ${LogoLink} img{
                width: 120px;
            }
        }
    ` : css`
        @media (min-width: 992px){
            padding: 1.5rem 0;
        }
    `}
`